.logo {
  display: grid;
  width: 108px;
  margin-right: 2.25rem;
}

nav {
  font-size: 1.125rem;
  line-height: 20px;
  text-decoration: none;
  color: hsl(0 0% 35%);

  li.links {
    margin-right: 1.5rem;
  }
}

nav a:hover {
  text-decoration: underline;
}

.main-cta {
  @include cta-button;
  width: 12.25rem;
  display: none;

  @media screen and (min-width: 1140px) {
    display: block;
  }
}

